
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

export default {
name: "subStoreList",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading,

},
data() {
return {
storeList: [],
store_list_page:1,
fanShow_bottom:false,
loading_bottom:false
};
},
created() {
this.getStoreList();
},
mounted(){
this.$nextTick(function() {
isScrollBottom(this.scrollBottomCB);
});
},
methods: {
async getStoreList() {

let data = {
plat: 3,
account: this.account,
token: this.token,
page: this.store_list_page
};

this.store_list_page++;
const [err, res] = await api.allMyShopList(data);

if (err) {
console.log("err", err);
return;
}




this.storeList = this.storeList.concat(res.data.list);


},
async resetPasswordFun(e) {

const _this = this; 
var shopName=e.target.dataset.name;
var shopSubId=e.target.dataset.id;
this.$vux.confirm.show({
content: "是否将子账号"+shopName+"密码重置为123456？",

onCancel() {
console.log(this); 
console.log(_this); 
},
async onConfirm() {//闭包问题
let data ={
plat: 3,
account: _this.account,
token: _this.token,
sub_member_id:shopSubId
};
const [err, res] =await api.resetManagePwd(data);
if(res && res.code=="2000"){
_this.$vux.toast.show({
text: '修改成功'
});
}


}
});
},
addNewAccount() {},

scrollBottomCB() {
this.getStoreList();
}
},
filters: {},
computed: {
...mapGetters(["user", "account", "token"])
},

};



